<!-- eslint-disable no-prototype-builtins -->
<template>
  <li>
        <label >
            <input type="checkbox"
             :checked="todata.done"
             @change="changecheck(todata.id)">
            <span v-show="!todata.isEdite">{{todata.title}}</span>
            <input type="text" v-show="todata.isEdite" :value="todata.title"
            @blur="blurItem(todata,$event)"
            ref="inputTitle">
        </label>
        <button class="btn btn-danger"  @click="deleteItem(todata.id)">删除</button>
        <button class="btn btn-editor" 
        @click="updataItem(todata)"
        v-show="!todata.isEdite"
       >编辑</button>
    </li>
</template>

<script>
import PubSub from 'pubsub-js';

export default {
   name:'UserItem',
   props:['todata'],
 methods: {
    changecheck(id){
        // this.$bus.$emit('checktodo',id)
        PubSub.publish('checktodo',id)
    },
    deleteItem(id){
    if(confirm('确定删除吗？')){
        this.$bus.$emit('deleteItems',id)
    }
    },
    updataItem(todo){
        // eslint-disable-next-line no-prototype-builtins
        if(todo.hasOwnProperty('isEdite')){
            todo.isEdite=true
        }
        else{
            this.$set(todo,'isEdite',true)
        }
        // 唯一输入设置,更新dom之后再执行当改变数据后，根据改变后的数据进行操作就需要这个
        this.$nextTick(
            function(){
                this.$refs.inputTitle.focus()
            }
        )
      
    
    },
    blurItem(item,e){
         item.isEdite=false
         this.$bus.$emit('updataTodo',item.id,e.target.value)
    }

 },
}
</script>

<style scoped>
 li{
        list-style:none;
        height: 36px;
        line-height: 36px;
        padding: 0 5px;
        border-bottom: 1px solid #ddd;
    }
li label{
    float:left;
    cursor:pointer;
}
/* li label li input{
    vertical-align: middle;
    margin-right: 6px;
    position: relative;
    top:-1px;
} */
li button{
    float: right;
    display: none;
    margin-top:3px;
}
li:before{
    content: initial;
}
li:last-child{
    border-bottom: none;
}
li:hover{
    background-color: #ddd;
}

li:hover button{
    display: block;
}
</style>