<template>
  <div class="todo-footer" v-show="total">
    <label>
      <input type="checkbox" v-model="totalchange">
    </label>
    <span>已完成 {{donetotal}}</span> / 全部 {{total}}
    <button class="btn btn-danger" @click="clearall()">清除已经完成的任务</button>
  </div>
</template>

<script>
export default {
name:'UserFooter',
props:['todos'],
computed:{
  total(){
      return this.todos.length
  },
  donetotal(){
    //统计被勾选的数量
    return this.todos.reduce((pre,todo)=> pre+(todo.done?1:0),0)
  },
  totalchange:{
     get() {
        return this.donetotal===this.total && this.total>0
      },
      set(value) {
        this.$emit('changeAll',value)
      //  this.changeAll(value)
      }
  }
},
methods: {
  clearall(){
    if(confirm('你是否删除所有已经完成的任务？'))
    this.$emit('clearDone')
    // this.clearDone()
  }
},
}
</script>

<style>

.todo-footer {
 height: 40px;
 line-height: 40px;
 padding-left: 6px;
 margin-top:5px;
}
.todo-footer label{
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}
.todo-footer label input{
 position:relative;
 top:-1px;
}
.todo-footer label input:focus{
  outline: none;
  border-color: rgba(82, 168, 236, 0.8);
  box-shadow: inset 0 1px 1px rgba(82, 168, 236, 0.8);
}
.todo-footer label input:focus{
  outline: none;
  border-color: rgba(82, 168, 236, 0.8);
  box-shadow: inset 0 1px 1px rgba(82, 168, 236, 0.8);
}
.todo-footer button{
  float:right;
}
</style>