<template>
 <div id="app">
  
  <div class="todo-container">
    <h2>开始你的每日计划！</h2>
    <div class="todo-wrap">
      <UserHeader @addtodo="addtodo"/>
      <UserList :todos="todos"/>
     <UserFooter :todos="todos" @changeAll="changeAll" @clearDone="clearDone"/>

    </div>
  </div>
 </div>
</template>

<script>
import PubSub from 'pubsub-js'
import UserHeader from './components/UserHeader.vue'
import UserList from './components/UserList.vue'
import UserFooter from './components/UserFooter.vue'


export default {
  name: 'App',
  components: {
    UserHeader,UserList,UserFooter
  },
  // 数组里面套对象
  data(){
    return {
      // 或一个空数组防止后面统计length出错
      todos:JSON.parse(localStorage.getItem('todos'))||[]
    }
  },
  methods: {
    addtodo(x){
     this.todos.unshift(x)
    },
    checktodo(_,x){
    //  遍历修改
    this.todos.forEach(todo => {
      if (todo.id==x) todo.done=!todo.done;
    });
    },
    deleteItems(x){
       this.todos= this.todos.filter(todo => todo.id!==x)
    },
    changeAll(x){
    //遍历修改状态
    this.todos.forEach(todo => todo.done=x)
    },
    clearDone(){
       this.todos=this.todos.filter((todo) => {
          return !todo.done
       })
    },
    updataTodo(id,newtitle){
      this.todos.forEach((todo) => {
        if(todo.id==id){
          todo.title=newtitle
        }
      })
    }
  },
  watch:{
    // 由于要监控对象里的数值改变，使用深度监控
    todos:{
      deep:true,
      handler(value){
      localStorage.setItem('todos',JSON.stringify(value))
    }
    }
    
  },
  mounted() {
    this.$bus.$on('deleteItems',this.deleteItems)
    // this.$bus.$on('checktodo',this.checktodo)
    this.pubId=PubSub.subscribe('checktodo',this.checktodo)
    this.$bus.$on('updataTodo',this.updataTodo)
  },
  destroyed() {
    this.$bus.$off(['deleteItems','checktodo','updataTodo'])
    PubSub.unsubscribe(this.pubId)
  },
}
</script>

